
import { Vue } from "vue-class-component";
import users from "@/store/modules/users";

export default class Login extends Vue {
  email = "";
  password = "";
  loginErr = "";

  login() {
    users
      .login({
        email: this.email,
        password: this.password,
      })
      .then(() => this.$router.push("/"))
      .catch((error) => {
        this.loginErr = error.message;
      });
  }
}
